import React from 'react'

export default props => (
<div style={{display:"flex"}}>
    <div
        style={{
            textDecoration: props.todo.complete ? "line-through": ""         
        }}  
        onClick={props.toggle}             
    >
        {props.todo.text}
    </div>
        
        <button onClick={props.onDelete}>Delete</button>
</div>);


